<template>
  <el-container v-loading="loading">
    <div class="box">
      <h3>{{activeheader}}</h3>
      <div class="input-box">
        <label for="" class="label require"><i>姓名：</i></label>
        <el-input class="input-width" v-model="userName" placeholder="请输入用户姓名"></el-input>
      </div>
      <div class="input-box">
        <label for="" class="label require"><i>姓别：</i></label>
        <el-select v-model="sex" placeholder="请选择用户性别">
          <el-option
            v-for="item in sexList"
            :key="item.dValue"
            :label="item.dValue"
            :value="item.dKey">
          </el-option>
        </el-select>
      </div>
      <div class="input-box">
          <label class="label require"><i>地区：</i></label>
          <user-address @countryChange="countryChange" :defaultValue="defaultAddress" ref="userfileAddress" seletStyle="width:217px"/>
      </div>
      <div class="input-box">
          <label class="label require"><i>所在医院：</i></label>
          <el-select v-model="hospitalId" placeholder="请选择" :disabled="hosArray.length < 1">
            <el-option
              v-for="item in hosArray"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
          </el-select>
          <span class="red" v-if="hosArray.length < 1">{{hosTips}}</span>
          <span class="add" v-if="hospitalId" @click="hosAdd">添加</span>
      </div>
      <div class="input-box" v-if="hosNameArray.length > 0">
        <label class="label"></label>
        <div class="input-tip">
          <span v-for="item in hosNameArray" :key="item.id">{{item.name}}</span>
        </div>
      </div>
      <div class="input-box">
        <label for="" class="label require"><i>身份证号：</i></label>
        <el-input class="input-width" v-model="paper" placeholder="请输入内容"></el-input>
      </div>
      <div class="input-box">
        <label for="" class="label require"><i>手机号码：</i></label>
        <el-input class="input-width" v-model="mobile" placeholder="请输入内容"></el-input>
      </div>
      <div class="input-box">
        <label for="" class="label require"><i>银行名称：</i></label>
        <el-input class="input-width" v-model="bank" placeholder="请输入内容"></el-input>
      </div>
      <div class="input-box">
        <label for="" class="label require"><i>银行账号：</i></label>
        <el-input class="input-width" v-model="bankCardNum" placeholder="请输入内容"></el-input>
      </div>
      <div class="input-box">
        <label for="" class="label"></label>
        <el-checkbox v-model="areaOwner" label="1">地区负责人</el-checkbox>
      </div>
      <div class="input-box">
        <label class="label"><i>人员技能标签：</i></label>
        <el-select v-model="skill" placeholder="请选择">
          <el-option
            v-for="item in skillTagList"
            :key="item.id"
            :label="item.dValue"
            :value="item.dKey">
          </el-option>
        </el-select>
        <span class="add" v-if="skill" @click="skillAdd">添加</span>
      </div>
      <div class="input-box" v-if="skillArry.length > 0">
        <label class="label"></label>
        <div class="input-tip">
          <span v-for="item in skillArry" :key="item.id">{{item.name}}</span>
        </div>
      </div>
      <div class="input-box">
        <label class="label"><i>人员排版选择：</i></label>
        <div>
          <el-checkbox-group v-model="dateAfterChecked">
              <el-checkbox v-for="date in dateMorList" :label="date.id" :key="date.id">{{date.name}}</el-checkbox>
          </el-checkbox-group>
          <el-checkbox-group v-model="dateAfterChecked">
              <el-checkbox v-for="date in dateAfterList" :label="date.id" :key="date.id">{{date.name}}</el-checkbox>
          </el-checkbox-group>
        </div>
      </div>
      <div class="input-box">
        <label class="label"><i>备注：</i></label>
        <el-input
          type="textarea"
          :rows="2"
          placeholder="请输入内容"
          v-model="remark">
        </el-input>
      </div>
      <div class="input-box box-last">
        <el-button type="primary" @click="submit">提交</el-button>
        <el-button @click="cancle">取消</el-button>
      </div>
    </div>
  </el-container>
</template>

<script>
import check from "utils/check";
import { sexList, skillTagList } from "data/listBody";
import UserAddress from "components/address";
import queryString from "querystring"

const headers = ["新增人员", "修改信息"];
const hosTips = ["请先选择地区","该地区没有对应医院信息,请先新建医院"];
const dateMorList = [
  {id:'11', name:'星期一上午'},
  {id:'21', name:'星期二上午'},
  {id:'31', name:'星期三上午'},
  {id:'41', name:'星期四上午'},
  {id:'51', name:'星期五上午'},
]
const dateAfterList = [
  {id:'12', name:'星期一下午'},
  {id:'22', name:'星期二下午'},
  {id:'32', name:'星期三下午'},
  {id:'42', name:'星期四下午'},
  {id:'52', name:'星期五下午'},
]
const requireArray = ["userName","sex","paper","mobile","bankCardNum","bank","province","hospitalIds"]; //必传字段
let hosChange = true; //是否请求医院列表
export default {
  name: "addUserfiled",
  data() {
    return {
      userName: "",
      sex: "",
      sexList,
      skillTagList,
      hosTips: hosTips[0],
      hospitalId:'',
      hospitalIdArray:[],
      skill:'',
      hosArray:[],
      hosNameArray:[],
      paper:"",
      mobile:"",
      bank:"",
      bankCardNum:"",
      areaOwner:"1",
      paper:"",
      paper:"",
      remark:"",
      loading: false,
      skillArry: [],
      hospitalArry: [],
      dateMorList: dateMorList,
      dateAfterList: dateAfterList,
      dateAfterChecked: [],
      defaultAddress: null, //默认地址
      activeheader: headers[0],
      check: check.methods.check,
      checkTip: check.tips.checkTip,
    };
  },
  components: {
    UserAddress
  },
  mounted() {
    this.editInit();
  },
  methods: {
    editInit() {
      let { type, id } = this.$route.query;
      if (type === "edit") this.activeheader = headers[1];
      if (!id) return;
      this.loading = true;
      this.api.get(`/groundPerson/${id}`)
        .then(res => {
          //查询产品详情
          this.dataInit(res.data);
          this.loading = false;
        })
        .catch(err => {
          console.log(err);
          this.loading = false;
          this.$message(err.data.message);
        });
    },
    dataInit(data) {
      data = data || {};
      let {areaOwner,bank,bankCardNum,province,city,country,hospitalIds,id,mobile,paper,remark,sex,skills,userName,workTimes} = data;

      this.userName = userName;
      this.areaOwner = areaOwner;
      this.bank = bank;
      this.bankCardNum = bankCardNum;
      this.id = id;
      this.mobile = mobile;
      this.paper = paper;
      this.remark = remark;
      this.sex = sex;
      let that = this;      
      if(hospitalIds){
        hosChange = false;
        const param = queryString.stringify({province, city, country});
        this.api.get(`/hospital?${param}`).then((res)=>{
            this.hosArray = res.data.list;
            let hospitalArr = hospitalIds.split(',');
            hospitalArr.map(item => {
              this.hosAdd(item)
            })
            setTimeout(()=>{
              hosChange = true;
            },1000)            
        })
      }
      
      if(skills){
        let skillArr = skills.split(',');
        skillArr.map(item => {
          this.skillAdd(item)
        })
      }

      if(workTimes){
        let workArr = workTimes.split(',');
        workArr.map(item => {
          this.dateAfterChecked.push(item)
        })
      }

      this.defaultAddress = {
        province,
        city,
        country
      };
    },
    hosAdd(key){
      let hosId = this.hospitalId;      
      key = parseInt(key);
      if(key) hosId = key;
      let hosArr = this.hosArray.filter(item => {
        return item.id == hosId;
      })
      let filterArry = this.hosNameArray.filter(item => {
        return item.id == hosId;        
      })
      let hosName = hosArr[0].name;
      if(!hosName) hosName = hosArr[0].id;;
      if(filterArry.length > 0) return;
      let hosNameArray = [].concat([],this.hosNameArray,{
          id: hosId,
          name: hosName
      })
      this.hosNameArray = hosNameArray;
    },
    skillAdd(key){
      let skillId = this.skill;  
      key = parseInt(key);          
      if(key) skillId = key;
      let skillArr = this.skillTagList.filter(item => {
        return item.dKey == skillId;
      })
      let filterArry = this.skillArry.filter(item => {
        return item.id == skillId;     
      })
      let skillName = skillArr[0].dValue;
      if(filterArry.length > 0) return;
      let skillArry = [].concat([],this.skillArry,{
          id: skillId,
          name: skillName
      })
      this.skillArry = skillArry;
    },
    submit() {
      let addressParams = this.$refs.userfileAddress.getData();
      let hospitalIds = this.hosNameArray.map(item => {
        return item.id;
      })
      hospitalIds = hospitalIds.join(',');

      let skills = this.skillArry.map(item => {
        return item.id;
      })
      skills = skills.join(',');

      let workTimes = this.dateAfterChecked.join(',');
      let params = {
        userName: this.userName,
        sex: this.sex,
        paper: this.paper,
        mobile: this.mobile,
        bankCardNum: this.bankCardNum,
        bank: this.bank,
        areaOwner: this.areaOwner,
        remark: this.remark,        
        hospitalIds,
        skills,
        workTimes,
      };
      let param = this.lodash.assign({}, params, addressParams);
      this.check(param, requireArray)
        .then(() => {
            this.addUser(param);
          }
        )
        .catch(err => {
          console.log(err);
          this.$message(err);
        });
    },
    editUser(param) {
      //修改用户
      param.id = this.id;
      this.loading = true;
      this.api.put("/groundPerson", param, "form")
        .then(res => {
          this.loading = false;
          this.$router.go(-1);
        })
        .catch(err => {
          console.log(err);
          this.loading = false;
          this.$message(err.data.message);
        });
    },
    addUser(param) {
      //添加用户
      if (this.$route.query.type === "edit") return this.editUser(param);
      this.loading = true;
      this.api.post("/groundPerson", param, "form")
        .then(res => {
          this.$router.go(-1);
          this.loading = false;
        })
        .catch(err => {
          console.log(err);
          this.loading = false;
          this.$message(err.data.message);
        });
    },
    countryChange(data){
      const param = queryString.stringify(data || {});
      if(!hosChange) return;
      this.api.get(`/hospital?${param}`).then((res)=>{
          this.hosArray = res.data.list;
          this.hospitalId = '';
          this.hosNameArray = [];
          if(this.hospitalArry < 1) this.hosTips = hosTips[1];
      }).catch((err) => {
          console.log(err)
          this.$message(err.message)
      })
    },
    cancle() {
      this.$router.go(-1);
    }
  }
};
</script>
 
<style lang="less" scoped>
.box {
  padding-left: 20px;
  h3 {
    margin-top: 10px;
    margin-bottom: 20px;
  }
}
.input-box {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
  margin-left: 10px;
  .input-tip {
    max-width: 800px;
    white-space: nowrap;
    overflow: auto;
    span {
      display: inline-block;
      margin-right: 10px;
    }
  }
  &.box-last {
    margin-left: 30px;
    margin-top: 20px;
  }
}
.label {
  width: 120px;
  min-width: 120px;
  text-align: right;
  display: inline-block;
  i {
    font-style: normal;
  }
  &.require {
    i {
      position: relative;
      &::after {
        position: absolute;
        content: "*";
        color: red;
        left: -10px;
        top: 2px;
      }
    }
  }
}
span{
  &.add{
    cursor: pointer;
    font-size: 12px;
  }
  &.red{
    color: red;
    font-size: 12px;
  }
}
.el-select{
  width: 216px;
}
.label-left {
  margin-left: 15px;
}
.input-width {
  width: 216px;
}
.el-textarea {
  width: 300px;
}
.el-select {
  margin-right: 20px;
}
</style>
 